
<template>
    <div id='cussrv'>
        <!--  -->
        <navig-bar></navig-bar>
        <!--  -->
        <div>
            <!--  -->
            <!--  -->
            <div style="margin-top:30%; margin-bottom: 10%">
                <van-image
                    round
                    width="120"
                    height="120"
                    v-bind:src=csAvatar
                ></van-image>
            </div>
            <!--ID-->
                <div id="showID">{{ServiceId}}</div>
            <van-cell-group>
                <van-cell title=":" :value="ServiceName"/>
                <van-cell title=":" :value="ServiceSex"/>
                <van-cell title=":" :value="ServicePhone"/>
            </van-cell-group>
        </div>

        <div>
            <van-button round type="warning" to="/" block></van-button>
        </div>

        <!--  -->
        <div style="margin-top:60px;">
            <tabBar></tabBar>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import navigBar from './navigBar';
    import tabBar from "./tabBar";
    // import { NavBar } from 'vant';
    // Vue.use(NavBar);
    new Vue({});
    export default {
    name:'Service',
    el: '#cussrv',
    components: {
        tabBar,
        navigBar
    },
    data() {
        return {
            csAvatar: "http://imagemxc.test.upcdn.net//-.png",
            ServiceId: "",
            ServiceName: "",
            ServiceSex: "",
            ServicePhone: "12345678910"
        }
    },
    methods: {
    }
    }
    import { Toast } from 'vant';
    Vue.use(Toast);
</script>

<style>
#showID{
    margin-bottom: 40px;
    font-size: 20px;
    font-weight: bolder;
    horiz-align: center;
    vertical-align: center;
}
</style>


