<template>
    <div>
        <h3></h3>
        <!--    -->
        <navig-bar></navig-bar>
        <!--    -->
        <van-cell-group>
            <van-cell title=":" :value="WorkOrderId" icon="records"/>
            <van-cell title=":" :value="RoomId" icon="wap-home-o"/>
            <van-cell v-if="RepairmanId" title=":" :value="RepairmanId" icon="manager-o"/>
            <van-cell v-if="ServiceId" title=":" :value="ServiceId" icon="service-o"/>
            <van-cell title=":" :value="WorkOrderItem" icon="notes-o"/>
        </van-cell-group>
        <van-button v-if="RepairmanId==''" style="margin-top: 50px; " round block type="primary" @click="showArrange"></van-button>
        <!---->
        <van-popup v-model="showArrageRPPanel" position="bottom" @close="cancelArrange" :style="{ height: '30%' }">
            <h5></h5>
            <van-form @submit="submitArrange">
                <van-field
                        v-model="arrange.RepairmanId"
                        name="ID"
                        label="ID"
                        placeholder="ID"
                        :rules="[{ required: true, message: 'ID' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        
                    </van-button>
                </div>
            </van-form>
        </van-popup>
        <!--    -->
        <tab-bar></tab-bar>

    </div>
</template>

<script>
    import navigBar from "../navigBar";
    import tabBar from "../tabBar";
    import Vue from 'vue';
    import {Toast}from 'vant';
    Vue.use(Toast);
    export default {
        name: 'WorkOrderSP',
        components: {
            navigBar,
            tabBar
        },
        created() {
            this.WorkOrderId=this.$route.params.id;
        },
        data(){
            return{
                WorkOrderId: '',
                RoomId: 'R1321',
                RepairmanId: '',
                ServiceId: '',
                WorkOrderItem: '',
                showArrageRPPanel: false,
                arrange: {
                    RepairmanId: '',
                    ServiceId: '',
                }
            }
        },
        methods:{
            showArrange(){
                this.showArrageRPPanel = true;
            },
            cancelArrange(){
                this.showArrageRPPanel = false;
            },
            submitArrange(){
                this.RepairmanId = this.arrange.RepairmanId;
                this.ServiceId = this.arrange.ServiceId; // ID============================================
                this.showArrageRPPanel = false;
                Toast.success("")
            }
        }
    }
</script>

<style scoped>

</style>


