<template>
    <div>
        <h3></h3>
        <!--    -->
        <navig-bar></navig-bar>
        <!--    -->
        <van-cell-group>
            <van-cell title=":" :value="OrderId" icon="records"/>
            <van-cell title=":" :value="RoomId" icon="wap-home-o"/>
            <van-cell title=":" :value="RentalEmail" icon="manager-o"/>
            <van-cell title=":" :value="OrderType" icon="service-o"/>
            <van-cell title=":" :value="OrderType" icon="service-o">
                <template #default>
                    <div v-if="AuditStatus==''" style="color: #40FF40"></div>
                    <div v-else-if="AuditStatus==''" style="color: #A9A9A9"></div>
                    <div v-else style="color: #FF0000"></div>
                </template>
            </van-cell>
            <van-cell title=":" :value="OrderItem" icon="notes-o"/>
        </van-cell-group>
        <div v-if="AuditStatus==''">
            <van-button style="margin-top: 50px; " round block type="primary" @click="auditAccept"></van-button>
            <van-button style="margin-top: 20px; " round block type="danger" @click="auditDeny"></van-button>
        </div>

        <div v-if="AuditStatus==''">
            <van-button v-if="OrderType=''" style="margin-top: 20px; " round block type="primary" @click="exportContract"></van-button>
        </div>

        <!--    -->
        <tab-bar></tab-bar>

    </div>
</template>

<script>
    import navigBar from "../navigBar";
    import tabBar from "../tabBar";
    import Vue from 'vue';
    import {Toast}from 'vant';
    Vue.use(Toast);
    export default {
        name: 'OrderSP',
        components: {
            navigBar,
            tabBar
        },
        created() {
            this.OrderId=this.$route.params.id;
        },
        data(){
            return{
                OrderId: '',
                RentalEmail: 'shang@163.com',
                RoomId: 'R1321',
                OrderType: '',
                OrderItem: '',
                AuditStatus: '',
            }
        },
        methods:{
            auditAccept(){
                Toast.success("");
                this.AuditStatus='';
            },
            auditDeny(){
                Toast.success("");
                this.AuditStatus='';
            },
            exportContract(){
                this.$router.push("/CS/ExportContract/"+this.OrderId);
            }
        }
    }
</script>

<style scoped>

</style>


