<template>
    <div>
        <!---->
        <van-search v-model="searchKey" clearable @search="onSearch" @clear="onClear" placeholder="" />
        <!---->
        <div v-bind:style="{top: hoverHeight+'px', left: hoverWidth+'px'}" style="position: fixed; z-index: 1" >
            <van-icon name="add-o" size="30px" @click="showNewHouse"></van-icon>
        </div>
        <!---->
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
            <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text=""
                    @load="onLoad"
            >
                <van-cell v-for="item in list" :key="item" :value="item.value" :title="item.title" title-style="text-align: left; margin-left: 20px" :icon="item.avatar" @click="onClickHouse(item.id)" >
                    <template #icon>
                        <van-image
                                width="45"
                                height="45"
                                v-bind:src="item.avatar"
                        >
                        </van-image>
                    </template>
                    <template #default>
                        <div style="font-size: 20px; color: #FF0000">{{item.value}}</div><div style="color: #FF0000">/</div>
                    </template>
                    <template #label>
                        <div v-bind:style="{width: commentWidth+'px'}"><div class="van-ellipsis">{{item.label}}</div></div>
                    </template>
                </van-cell>
            </van-list>
        </van-pull-refresh>
        <!--    -->
        <van-popup round position="bottom" :style="{ height: '90%' }" @close="cancelNewHouse" v-model="showNewHousePanel">
            <van-form @submit="submitNewHouse">
                <h5></h5>
                <van-field
                        v-model="newHouse.RoomType"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="newHouse.RoomAvailable"
                        name=""
                        label=""
                        placeholder="(true/false)"
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="newHouse.RoomRent"
                        type="number"
                        name=""
                        label=""
                        placeholder="()"
                        :rules="[{ required: true, message: '()' }]"
                />
                <van-field
                        v-model="newHouse.OwnerName"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="newHouse.OwnerPhone"
                        name=""
                        type="tel"
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="newHouse.RoomAddress"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="newHouse.RoomItem"
                        type="textarea"
                        :autosize="{maxHeight: 150, minHeight: 150}"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-button style="margin-bottom: 10px" round block type="info" native-type="submit">
                    
                </van-button>
            </van-form>
        </van-popup>
    </div>
</template>

<script>
    import Vue from 'vue';
    import {Toast} from 'vant';
    Vue.use(Toast);
    export default {
        name: 'AllHouseList',
        data(){
            return{
                houseList: [
                    { id: '1', title: '15',value: '1000', label: "3815", avatar: "https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=402336215,3875604598&fm=26&gp=0.jpg"},
                    { id: '2', title: '', value: '2400', label: "1728", avatar: "https://pic1.58cdn.com.cn/anjuke_58/b4a301ce08762cc39cf2363242fc42af?w=696&h=522&crop=1&t=1"}
                ],
                list: [

                ],
                commentWidth: document.documentElement.clientWidth-150,
                loading: false,
                refreshing: false,
                finished: false,
                searchKey: '',
                hoverHeight: document.documentElement.clientHeight-150,
                hoverWidth: document.documentElement.clientWidth-50,
                showNewHousePanel: false,
                newHouse: {
                    RoomType: '',
                    RoomAvailable: true,
                    RoomRent: '999999',
                    OwnerName: '',
                    OwnerPhone: '',
                    RoomItem: '',
                    RoomAddress: ''
                }
            }
        },
        methods: {
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 
                this.finished = false;

                // 
                //  loading  true
                this.loading = true;
                this.onLoad();
            },
            onLoad() {
                // 
                // setTimeout  ajax 
                setTimeout(() => {
                    for (let i = 0; i < this.houseList.length; i++) {
                        this.list.push(this.houseList[i]);
                    }

                    // 
                    this.loading = false;
                    this.finished = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
            },
            onSearch(){
                this.list=[];
                for(let i=0;i<this.houseList.length; i++){
                    if(this.houseList[i].title.search(this.searchKey)!=-1)
                        this.list.push(this.houseList[i])
                }
            },
            onClear(){
                this.searchKey='';
                this.refreshing=true;
                this.onRefresh();
            },
            onClickHouse(houseId){
                this.$router.push({path: '/CS/CSRooms/'+houseId});
            },
            showNewHouse(){
                this.showNewHousePanel = true;
            },
            cancelNewHouse(){
                this.showNewHousePanel = false;
            },
            submitNewHouse(){
                Toast.success("");
                this.showNewHousePanel = false;
                this.refreshing=true;
                this.onRefresh();
            }
        }
    }
</script>

<style scoped>

</style>


