<template>
  <div class="MyInfo">
    <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>

    <van-row type="flex" justify="end">
      <h2><a :href="islogin" @click="judge" style="color:blue;">{{myid}}</a></h2>
    </van-row>
    <h1>{{ msg }}</h1>
    <h2></h2>

    <div>
        <van-cell v-for="(item,index) in List" :key="index" clickable @click=comment(item)>
            {{List[index]}}
        </van-cell>
    </div>

    <van-popup v-model="show" closeable round :style="{height:'45%',width:'100%'}">
      <div style="margin-top:32px;">
        <h1></h1>
        <van-rate v-model="value" touchable="true" />
        <van-field v-model="message" rows="2" autosize label="" type="textarea" maxlength="50" label-width="35px" placeholder="" show-word-limit/>
        <form>
        <van-button type="info" @click="submit"></van-button>
        </form>
      </div>
    </van-popup>

    <div>
    <p></p>
    :{{phone}} :{{email}} :{{weixin}}
    </div>
    

    <div style="margin-top:16px;">
        <Foot_bar></Foot_bar>
    </div>
  </div>
</template>

<script>
import Foot_bar from './Foot_bar';
import axios from 'axios';
import { Toast } from 'vant';

export default {
  name: 'Setting',
  data () {
    return {
      msg: '/',
      message:'',
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      List:['1','2','3'],
      value: 5,
      show:false,
      repairer:'',
      phone:'17815566221',
      email:'1322496098@qq.com',
      weixin:'17815566221',
      imgSrc:require('../assets/pugongying.jpg'),
    }
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid==""){
              this.islogin="/";
          }
      },
      comment(name){
          this.show=true;
          this.repairer=name;
      },
      submit(){
          location.reload();
          this.$router.go(0);
          Toast('');
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.background{
    width:100%;  
    height:100%;  /**100% */
    z-index:-1;
    position: fixed;
}
</style>



